.button {

    color: inherit;
    background: #eaf2f1;
    margin: 5px;
    width: 80px;
    padding: 5px 0;
    border-radius: 5px;
    text-align: center;
    border: none;
    font: inherit;
    font-size: 18px;




}

.button:not(.disabled):hover {
    filter: brightness(80%);




}

.button:not(.disabled):active {

    transform: scale(0.95);
}


.disabled {
    color: whitesmoke;
    background: gray;
    opacity: 0.5;
}

